import axios from 'axios'
import BigInt from 'json-bigint'
import Router from '@/router'
import { Message } from 'element-ui'
import { getUser, setUser } from '@/utils/store'
// import LoadingManage from './loading'
// create an axios instance
const service = axios.create({
  baseURL: process.env.BASEURL, // api  base_url
  timeout: 10000, // request timeout
  transformResponse (data) {
    if (data) { return BigInt.parse(data) } // id json-bigint 
  }
})
// request
service.interceptors.request.use(
  config => {
    // LoadingManage.openLoading(); //
    const Authorization = getUser()
    if (Authorization && Authorization.token) {
      //  
      config.headers['Content-Type'] = 'application/json'
      config.headers.token = Authorization.token
    }
    // token-- ['X-Token']key
    return config
  },
  error => {
    Promise.reject(error)
  }
)

// response
service.interceptors.response.use(
  /**
   *    
   */
  response => {
    if (response.headers && response.headers.refresh_token) {
      const user = getUser()
      if (user) {
        user.token = response.headers.refresh_token
        setUser(user)
      }
    }
    // LoadingManage.closeLoading() //loading
    // 
    if (response.data && response.data.code) {
      const code = response.data.code
      if (code > 49 && code < 70) {
        Router.replace({ path: '/login' })
      }
    }
    return response.data
  },
  error => {
    // LoadingManage.closeLoading() // loading
    let message = ''
    const code = error.response ? error.response.status : ''
    switch (code) {
      case 400:
        message = ''
        break
      case 401:
        message = 'token'
        break
      case 403:
        message = ''
        break
      case 404:
        message = ''
        break
      case 500:
        message = ''
        break
      case 507:
        message = ''
        break
      default :
        message = ''
    }
    // message = message + ':' +  error.response ? error.response.data.message : error.response.data.message
    if (message) {
      Message({
        message,
        type: 'warning',
        onClose: code === '401' ? () => Router.replace({ path: '/login' }) : null
      })
    }
    return new Promise(function () {}) // promise
  }
)

export default service



